/* 
    === Разметка верхней части шапки (лого + меню) ===
*/
.page-header {
    background:  
    url(../img/back-mobile-1-x-jpg.png) no-repeat;
    background-size: 100%;
}
.page-header__top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
    align-items: center;
    min-height: 66px;
    background-color:rgba(0,0,0,.3);
    overflow: hidden;
    position: relative;
}
.page-header__logo {
    padding-left: 20px;
}
.page-header__menu {
    position: relative;
    width: 50px;
    height: 24px;
    background: url('../img/menu-burger-svg.svg');
    background-size: 50px 24px;
    margin-right: 20px;
}
.main-nav {
    position: absolute;
    top: 67px;
    z-index: 1;
    display: none;
    padding: 0;
    margin: 0;
    list-style: none;
    width: 100%;
}
.nav-list {
    height: 256px;
    width:100%;
    margin: 0;
    border-bottom: 1px solid #000;
    clip: initial;
    position: relative;
    visibility: visible;
}
.nav-list__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-height: 63px;
    background-color:#283645;
    box-shadow: 0 -1px 0 #000,
     inset 0 1px 0 rgba(255,255,255,.15);
}
.nav-list__item-link {
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    line-height: 65px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    color:#ffffff;
}
/*
    /=== Конец разметки верхней части шапки (лого + меню) ===
*/

/* === Разметка средней части шапки === */
.page-header__heading {
    position: relative;
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    font-weight: 300;
    max-width: 225px;
    text-align: center;
    color:#ffffff;
    margin:0 auto;
    margin-top: 25px;
    margin-bottom: 33px;
}
.page-header__image {
    width: inherit;
    height: inherit;
}